import {FilterEntityClass} from './filter.class';

export class SingleFilterClass extends FilterEntityClass {
  usedFiltersCount = 0;
  selectedField = null;
  uiLabel = '';
  selectType:string = 'radio';
  isRadioGroup:boolean = true;

  constructor(data) {
    super(data);
    this.displayName = data.displayName;
    this.selectType = data.formType || this.selectType;
    this.isRadioGroup = this.selectType === 'radio';
  }

  resetFilters() {
    this.usedFiltersCount = 0;
    this.selectedField = null;
    this.isSelected = false;
    this.updateLabel();
  }

  setModalResult(result) {
    //This will get the first object property value
    this.selectedField = result[Object.keys(result)[0]]; //since this is a result from a single select reactive form, the return is an object with one property and value set

    this.isSelected = this.usedFiltersCount > 0;
    this.updateLabel();
  }

  updateLabel() {
    let selectedValueText = 'Any';
    if (this.selectedField) {
      //SourceData selected value displayname is the UiLabel
      selectedValueText = this.sourceData.length > 0 ? this.sourceData.find(r => r.fieldName.toString() === (this.selectedField)).displayName : 'Any';
    }

    this.uiLabel = selectedValueText;
  }

  getClosedPopupData(modalRef) {
    return modalRef.getFilterFormData();
  }

  getData() {
    return this.selectedField;
  }

  populateData(data) {
    const fieldData = data.split(',');

    this.selectedField = data;

    this.usedFiltersCount = fieldData.length;
    this.isSelected = this.usedFiltersCount > 0;
    this.updateLabel();
  }
}
