import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'app-nearby-subway-modal',
  templateUrl: './nearby-subway-modal.component.html',
  styleUrls: ['./nearby-subway-modal.component.scss']
})
export class NearbySubwayModalComponent implements OnInit {
  private triggerElementRef: ElementRef;

  subwayData: any[];
  isAllSelected: boolean = false;

  @ViewChild('subwayList', { read: ElementRef }) subwayList: ElementRef;

  constructor(
    public filterRef: MatDialogRef<NearbySubwayModalComponent>,
    @Inject(MAT_DIALOG_DATA)
    public data: {
      triggerRef: ElementRef;
      sourceData: any[];
    }
  ) {
    this.triggerElementRef = data.triggerRef;
    this.subwayData = data.sourceData;
  }

  ngOnInit() {
    const rect = this.triggerElementRef.nativeElement.getBoundingClientRect();

    console.log(rect, rect.bottom);
    this.filterRef.updatePosition({
      left: `${rect.left - 50}px`,
      top: `${rect.bottom + 20}px`
    });

    this.updateSelectedButtons();
  }

  selectAll() {
    this.isAllSelected = true;
    this.subwayData.forEach(i => (i.isSelected = true));
  }

  clearAll() {
    this.isAllSelected = false;
    this.subwayData.forEach(i => (i.isSelected = false));
  }

  onDoneClick(): void {
    this.filterRef.close({ data: this.subwayData });
  }

  updateSelectedButtons() {
    this.isAllSelected = this.subwayData.length == this.subwayData.filter(i => i.isSelected).length;
  }

  isSelectAllActive() {
    return {
      active: this.subwayData.some(i => !i.isSelected)
    };
  }

  isClearAllActive() {
    return {
      active: this.subwayData.some(i => i.isSelected)
    };
  }

  getData() {
    return this.subwayData;
  }
}
