import {Component, OnInit, Input, Inject, ChangeDetectorRef} from '@angular/core';
import {FiltersModel} from '../../filter-models/filters.class';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {FormControl, FormGroup} from "@angular/forms";
import {CustomValidators} from '../../../../../../shared/custom-validation/custom-validation';
import {HouseholdTypeEnum} from "../../../../../../shared/enums/developmentEnums";
import {customValidatorFn, VisibleFiltersConfInterface} from "./lottary.validation.class";

@Component({
  selector: 'app-lottery-filters-modal',
  templateUrl: './lottery-filters-modal.component.html',
  styleUrls: ['./lottery-filters-modal.component.scss']
})
export class LotteryFiltersModalComponent implements OnInit {
  @Input() filtersModel: FiltersModel;

  tempFiltersModel: FiltersModel = new FiltersModel();

  householdSizes: any[];
  propertyTypes: any[];
  unitLayoutTypes: any[];
  amenities: any[];
  neighborhoods: any[];
  subways: any[];
  visibleFiltersConf: VisibleFiltersConfInterface<FiltersModel>;

  householdIncomeFormControl: FormControl = new FormControl();
  householdSizeFormControl: FormControl = new FormControl();
  rentMinFormControl: FormControl = new FormControl();
  rentMaxFormControl: FormControl = new FormControl();
  priceMinFormControl: FormControl = new FormControl();
  priceMaxFormControl: FormControl = new FormControl();
  amenitiesFormControl: FormControl = new FormControl();
  ownershipTypeFormControl: FormControl = new FormControl();
  subwaysFormControl: FormControl = new FormControl();
  unitLayoutTypeFormControl: FormControl = new FormControl();
  neighborhoodsFormControl: FormControl = new FormControl();

  filtersForm: FormGroup;

  //public minPricePlaceholder: number = 1;
  //public maxPricePlaceholder: number = 1000000;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dialogRef: MatDialogRef<LotteryFiltersModalComponent>,
    private cdRef: ChangeDetectorRef
  ) {
    this.filtersModel = this.data.filtersModel;

    this.householdSizes = this.data.filtersData.householdSizes;
    this.unitLayoutTypes = this.data.filtersData.unitLayoutTypes;
    this.amenities = this.data.filtersData.amenities;
    this.neighborhoods = this.data.filtersData.neighborhoods;
    this.subways = this.data.filtersData.subways;
    this.propertyTypes = this.data.filtersData.propertyTypes;
    this.visibleFiltersConf = this.data.visibleFiltersConf;
  }

  createForm() {
    let validatorFn = (this.filtersModel.householdType === HouseholdTypeEnum.Rentals) ?
      customValidatorFn('rentMinFormControl', 'rentMaxFormControl')
      : customValidatorFn('priceMinFormControl', 'priceMaxFormControl');


    this.filtersForm = new FormGroup({
      householdIncomeFormControl: this.householdIncomeFormControl,
      householdSizeFormControl: this.householdSizeFormControl,
      rentMinFormControl: this.rentMinFormControl,
      rentMaxFormControl: this.rentMaxFormControl,
      priceMinFormControl: this.priceMinFormControl,
      priceMaxFormControl: this.priceMaxFormControl,
      amenitiesFormControl: this.amenitiesFormControl,
      ownershipTypeFormControl: this.ownershipTypeFormControl,
      subwaysFormControl: this.subwaysFormControl,
      unitLayoutTypeFormControl: this.unitLayoutTypeFormControl,
      neighborhoodsFormControl: this.neighborhoodsFormControl
    }, validatorFn);
  }


  ngOnInit() {
    this.createForm();

    Object.assign(this.tempFiltersModel, this.filtersModel);

    // set default value
    //if (this.visibleFiltersConf.monthlyRent && !this.tempFiltersModel.monthlyRent) {
    //  this.tempFiltersModel.monthlyRent = { minRent: 0, maxRent: 10000 };      
    //}

    //if (this.visibleFiltersConf.price && !this.tempFiltersModel.price) {
    //  this.tempFiltersModel.price = { minPrice: 0, maxPrice: 1000000 };
    //}

    this.householdIncomeFormControl.setValue(this.tempFiltersModel.householdIncome);
    this.householdSizeFormControl.setValue(this.tempFiltersModel.householdSize);

    if (this.tempFiltersModel.monthlyRent) {
      this.rentMinFormControl.setValue(this.tempFiltersModel.monthlyRent.minRent);
      this.rentMaxFormControl.setValue(this.tempFiltersModel.monthlyRent.maxRent);
    } else {
      this.tempFiltersModel.monthlyRent = {
        minRent: null,
        maxRent: null
      }
    }


    if (this.tempFiltersModel.price) {
      this.priceMinFormControl.setValue(this.tempFiltersModel.price.minPrice);
      this.priceMaxFormControl.setValue(this.tempFiltersModel.price.maxPrice);
    } else {
      this.tempFiltersModel.price = {
        minPrice: null,
        maxPrice: null
      };
    }
    
    this.filtersForm.controls['priceMaxFormControl'].setValidators([CustomValidators.minNumber(this.priceMinFormControl)]);
    this.filtersForm.controls['rentMaxFormControl'].setValidators([CustomValidators.minNumber(this.rentMinFormControl)]);
    this.filtersForm.controls['priceMinFormControl'].setValidators([CustomValidators.maxNumber(this.priceMaxFormControl)]);
    this.filtersForm.controls['rentMinFormControl'].setValidators([CustomValidators.maxNumber(this.rentMaxFormControl)]);

    this.rentMinFormControl.valueChanges.subscribe(value => this.tempFiltersModel.monthlyRent.minRent = value);
    this.rentMaxFormControl.valueChanges.subscribe(value => this.tempFiltersModel.monthlyRent.maxRent = value);
    this.priceMinFormControl.valueChanges.subscribe(value => this.tempFiltersModel.price.minPrice = value);
    this.priceMaxFormControl.valueChanges.subscribe(value => this.tempFiltersModel.price.maxPrice = value);

    //this.priceMaxFormControl.setValidators([CustomValidators.minNumber(this.priceMinFormControl)]);
    this.amenitiesFormControl.setValue(this.tempFiltersModel.amenities);
    this.ownershipTypeFormControl.setValue(this.tempFiltersModel.ownershipType);
    this.subwaysFormControl.setValue(this.tempFiltersModel.subways);
    this.unitLayoutTypeFormControl.setValue(this.tempFiltersModel.unitLayoutTypes);
    this.neighborhoodsFormControl.setValue(this.tempFiltersModel.neighborhoods);

    this.householdIncomeFormControl.valueChanges.subscribe(value => {
      this.tempFiltersModel.householdIncome = value
    });
    this.householdSizeFormControl.valueChanges.subscribe(value => this.tempFiltersModel.householdSize = value);

    this.amenitiesFormControl.valueChanges.subscribe(value => this.tempFiltersModel.amenities = value);
    this.ownershipTypeFormControl.valueChanges.subscribe(value => this.tempFiltersModel.ownershipType = value);
    this.subwaysFormControl.valueChanges.subscribe(value => this.tempFiltersModel.subways = value);
    this.unitLayoutTypeFormControl.valueChanges.subscribe(value => this.tempFiltersModel.unitLayoutTypes = value);
    this.neighborhoodsFormControl.valueChanges.subscribe(value => this.tempFiltersModel.neighborhoods = [...value]);
  }

  onChangeMinPrice(event) {
    let min = parseInt(event);
    min = (this.tempFiltersModel.price && min >= this.tempFiltersModel.price.maxPrice ? 0 : min);
    this.tempFiltersModel.price.minPrice = min;
  }

  onChangeMaxPrice(event) {
    let max = parseInt(event);
    max = (this.tempFiltersModel.price && max <= this.tempFiltersModel.price.minPrice ? 1000000 : max);
    this.tempFiltersModel.price.maxPrice = max;
  }

  //validatePrice(c: AbstractControl): ValidatorFn {
  //  if (c.get('priceMinFormControl') >= c.get('priceMaxFormControl')) return 
  //    //this.tempFiltersModel.price.minPrice >= this.tempFiltersModel.price.maxPrice) {
  //    return { range: true };
  //  }
  //  return null;
  //}

  closeAndSearch() {
    this.dialogRef.close(this.tempFiltersModel);
  }
}
