import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-pure-carousel',
  templateUrl: 'pure-carousel.component.html',
  styleUrls: ['pure-carousel.component.scss']
})
export class PureCarouselComponent implements OnInit {
  @Input() data: any[] = [];
  activeIndex = 0;

  constructor() {}

  ngOnInit() {}

  onNextSlide() {
    this.activeIndex = ++this.activeIndex;
  }

  onPreSlide() {
    this.activeIndex = --this.activeIndex;
  }

  isLastSlideActive() {
    return this.data.length - 1 === this.activeIndex;
  }

  isFirstSlideActive() {
    return 0 === this.activeIndex;
  }

  jumpToSlide(i) {
    this.activeIndex = i;
  }
}
