import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BrowseLotteriesComponent } from './browse-lotteries.component';

const routes: Routes = [
  {
    path: '',
    component: BrowseLotteriesComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BrowseLotteriesRoutingModule {}
