import {OwnerTypeEnum, HouseholdTypeEnum} from "../../../../../shared/enums/developmentEnums";
import {ClassHelper} from "../../../../../core/class-helper";

export interface MonthlyRentRangeInterface {
  minRent: number;
  maxRent: number;
}

export interface PriceRangeInterface {
  minPrice: number;
  maxPrice: number;
}

export interface PropertyTypeInterface {
  name: string;
  id: string;
}

export interface NeighborhoodInterface {
  name: string;
  id: string;
  neighborhoodId: string;
  isSelected: boolean;
  subList: any[];
  boroughId: string;
}

export interface SubwayInterface {
  name: string;
  color: string; //background color
  fontColor: string; //foreground color
}

export interface AmenitiesInterface {
  // name: string;
  // id: string;
  amenityName: string;
  amenityTypeId: string;
}

export interface UnitLayoutTypeInterface {
  name: string;
  // id: string;
  unitLayoutTypeId: string;
}

export interface FiltersModelInterface {
  householdType: HouseholdTypeEnum | string | number;
  householdIncome: number;
  householdSize: number;
  unitLayoutTypes: UnitLayoutTypeInterface[];
  monthlyRent: MonthlyRentRangeInterface;
  price: PriceRangeInterface;
  ownershipType: OwnerTypeEnum[];
  neighborhoods: NeighborhoodInterface[];
  subways: SubwayInterface[];
  amenities: AmenitiesInterface[];
}

export class FiltersModel extends ClassHelper implements FiltersModelInterface {
  householdType = HouseholdTypeEnum.Rentals;
  householdIncome = null;
  householdSize = null;
  unitLayoutTypes = [];
  monthlyRent = null;
  price = null;
  ownershipType = [];
  neighborhoods = [];
  subways = [];
  amenities = [];

  constructor(data?: any) {
    super();
    this.setData(data);
  }

  count() {
    var count = 0;
    if (this.householdIncome) count++;
    if (this.householdSize) count++;
    if (this.monthlyRent) count++;
    if (this.price) count++;

    if (this.unitLayoutTypes) count += this.unitLayoutTypes.length;
    if (this.ownershipType) count += this.ownershipType.length;
    if (this.neighborhoods) count += this.neighborhoods.length;
    if (this.subways) count += this.subways.length;
    if (this.amenities) count += this.amenities.length;

    //householdType is ignored since it is always set
    return count;
  }
}
