import { FilterEntityClass } from './filter.class';
import * as _ from 'lodash';

export class ListFilterClass extends FilterEntityClass {
  usedFiltersCount = 0;
  selectedFields = {};

  constructor(data) {
    super(data);
    this.displayName = data.displayName;
    this.uiLabel = this.updateLabel();
  }

  resetFilters() {
    this.usedFiltersCount = 0;
    this.selectedFields = {};
    this.isSelected = false;
    this.uiLabel = this.updateLabel();
  }

  setModalResult(result) {
    this.selectedFields = result;
    this.usedFiltersCount = Object.keys(this.selectedFields).filter(
      fieldName => this.selectedFields[fieldName]
    ).length;

    this.isSelected = this.usedFiltersCount > 0;
    this.uiLabel = this.updateLabel();
  }

  updateLabel() {
    return this.updateLabelSelected(this.usedFiltersCount);
    //this.usedFiltersCount ? `${this.usedFiltersCount} Selected` : 'Any';
  }

  getClosedPopupData(modalRef) {
    return modalRef.getFilterFormData();
  }

  getData() {
    return _.pickBy(this.selectedFields);
  }

  populateData(data) {
    const fieldData = data.split(',');
    fieldData.forEach((fieldName, idx, fieldArray) => {
      this.selectedFields[fieldName] = true;
    });
    this.usedFiltersCount = fieldData.length;
    this.isSelected = this.usedFiltersCount > 0;
    this.uiLabel = this.updateLabel();
  }
}
