import {
  Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, AfterViewInit,
} from '@angular/core';
import {FiltersModel} from '../../filter-models/filters.class';
import * as _ from 'lodash';

@Component({
  selector: 'app-lottery-filters-summary',
  templateUrl: './lottery-filters-summary.component.html',
  styleUrls: ['./lottery-filters-summary.component.scss']
})
export class LotteryFiltersSummaryComponent implements AfterViewInit, OnChanges {
  labelsModel: any = [];
  @Input() filtersModel: FiltersModel = {} as FiltersModel;
  @Input() filtersData: FiltersModel = {} as FiltersModel;
  @Output() onChangeFilterEvent: EventEmitter<FiltersModel> = new EventEmitter();

  constructor() {
  }

  ngAfterViewInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
   
    if (
      changes.filtersModel &&
      changes.filtersModel.currentValue != changes.filtersModel.previousValue
    ) {
      this.transformToLabels(changes.filtersModel.currentValue)
    }
  }

  clearFilter(filterGroup, deletedFilter) {
    this.deleteFilterItemAndUpdateFilterMode(filterGroup, deletedFilter);
    this.onChangeFilterEvent.emit(this.filtersModel);
    this.transformToLabels(this.filtersModel)
  }

  deleteFilterItemAndUpdateFilterMode(filterGroup, deletedFilter) {
    const filterModel = this.filtersModel[filterGroup.filterType];


    if (_.isObject(filterModel) && !_.isArray(filterModel)) {
      this.filtersModel[filterGroup.filterType] =
        _.keys(filterModel)
          .forEach(key => {
            filterModel[key] = null;
          });
    }

    if (_.isString(filterModel) || _.isNumber(filterModel)) {
      this.filtersModel[filterGroup.filterType] = null;
    }

    if (_.isArray(filterModel)) {
      this.filtersModel[filterGroup.filterType] = filterModel.filter(item => {
        return item != deletedFilter.value
      })
    }
  }

  clearAll() {
    const type = this.filtersModel.householdType;
    this.filtersModel = new FiltersModel();
    this.filtersModel.householdType = type;
    this.onChangeFilterEvent.emit(this.filtersModel);
    this.transformToLabels(this.filtersModel)
  }

  transformToLabels(data) {

    if(!this.filtersData){
      return;
    }
    this.labelsModel = [];
    _.keys(data).forEach(filterType => {
      const filterValue = data[filterType];
      if (_.isEmpty(filterValue)) {
        return;
      }
      if (filterType == 'monthlyRent' && !filterValue.maxRent && !filterValue.minRent) {
        return;
      }

      if (filterType == 'price' && !filterValue.maxPrice && !filterValue.minPrice) {
        return;
      }

      const filterData = this.getLabelByFilterType(filterType, filterValue);

      if (filterData) this.labelsModel.push({...filterData, filterType: filterType});
    });
  }

  getLabelByFilterType(filterType: string, filterValue: any): object {
    switch (filterType) {
      // case 'householdType':
      //   return this.getFilterHouseholdType(filterValue);
      case 'householdIncome':
        return this.getFilterIncome(filterValue);
      case 'amenities':
        return this.getFilterAmenities(filterValue);
      case 'householdSize':
        return this.getFilterHouseholdSize(filterValue);
      case 'monthlyRent':
        return this.getFilterMonthlyRent(filterValue);
      case 'neighborhoods':
        return this.getFilterNeighborhoods(filterValue);
      case 'ownershipType':
        return this.getFilterOwnershipType(filterValue);
      case 'price':
        return this.getFilterPrice(filterValue);
      case 'subways':
        return this.getFilterSubways(filterValue);
      case 'unitLayoutTypes':
        return this.getFilterUnitLayoutTypes(filterValue);
      default:
        return null;
    }
  }

  getFilterHouseholdType(filterValue) {
    return {
      title: `household type:`,
      filterValue: [{
        label: `${filterValue}`,
        value: filterValue
      }]
    }
  }

  getFilterIncome(filterValue) {
    return {
      title: `income:`,
      filterValue: [{
        label: `$${filterValue}`,
        value: filterValue
      }]
    };
  }

  getFilterAmenities(filterValue) {
    const sourceData = this.filtersData['amenities'];
    if (!sourceData) {
      return {}
    }
    const filterLabels2 = sourceData
      .filter(item => filterValue.indexOf(item.amenityTypeId) >= 0)
      .map(item => ({
        label: item.amenityName,
        value: item.amenityTypeId
      }));

    return {
      title: `amenit${filterValue.length > 1 ? 'ies' : 'y'}:`,
      filterValue: filterLabels2
    };
  }

  getFilterHouseholdSize(filterValue) {
    const householdSizeData = this.filtersData['householdSizes'];
    if (!householdSizeData) {
      return {}
    }
    const filterLabels3 = householdSizeData
      .filter(item => (item.value + '') == filterValue)
      .map(item => ({
        label: item.name,
        value: item.value
      }));

    return {
      title: `room for:`,
      filterValue: filterLabels3
    };
  }

  getFilterMonthlyRent(filterValue) {
    if (filterValue.minRent && !filterValue.maxRent) return {
      title: `min rent:`,
      filterValue: [{
        label: `$${filterValue.minRent}`,
        value: filterValue.minRent
      }]//only min rent entered
    };
    else if (!filterValue.minRent && filterValue.maxRent) return {
      title: `max rent:`,
      filterValue: [{
        label: `$${filterValue.maxRent}`,
        value: filterValue.maxRent
      }] //only max rent entered
    };
    else if (!filterValue.minRent && !filterValue.maxRent) return {
      title: '',
      filterValue: [{}]
    };  // no rents entered
    else return {
        title: `rent:`,
        filterValue: [{
          label: `$${filterValue.minRent} - $${filterValue.maxRent}`,
          minRent: filterValue.minRent,
          maxRent: filterValue.maxRent
        }] // both rents entered
      };
  }

  getFilterNeighborhoods(filterValue) {
    return {
      title: `neighborhood:`,
      filterValue: filterValue.map(item => {
        return {
          label: item.title || item.name,
          value: item
        }
      })
    };
  }

  getFilterOwnershipType(filterValue) {
    const ownershipData = this.filtersData['propertyTypes'];
    if (!ownershipData) {
      return {}
    }
    const filterLabels1 = ownershipData
      .filter(item => filterValue.indexOf(item.id) >= 0)
      .map(item => ({
        label: item.name,
        value: item.id
      }));

    return {
      title: `owner type${filterValue.length > 1 ? 's' : ''}:`,
      filterValue: filterLabels1
    };
  }

  getFilterPrice(filterValue) {
    if (filterValue.minPrice && !filterValue.maxPrice) return {
      title: `min price:`,
      filterValue: [{
        label: `$${filterValue.minPrice}`,
        value: filterValue.minPrice
      }]//only min price entered
    };
    else if (!filterValue.minPrice && filterValue.maxPrice) return {
      title: `max price:`,
      filterValue: [{
        label: `$${filterValue.maxPrice}`,
        value: filterValue.maxPrice
      }]//only max price entered
    };
    else if (!filterValue.minPrice && !filterValue.maxPrice) return {
      title: '',
      filterValue: [{}]
    };  // no prices entered
    else return {
        title: `price:`,
        filterValue: [{
          label: `$${filterValue.minPrice} - $${filterValue.maxPrice}`,
          minPrice: filterValue.minPrice,
          maxPrice: filterValue.maxPrice
        }] // both prices entered
      };
  }

  getFilterSubways(filterValue) {
    return {
      title: `near subway line${filterValue.length > 1 ? 's' : ''}:`,
      filterValue: filterValue.map(subwayValue => ({
        label: subwayValue,
        value: subwayValue
      }))
    };
  }

  getFilterUnitLayoutTypes(filterValue) {
    const sourceData = this.filtersData["unitLayoutTypes"];
    if (!sourceData) {
      return {}
    }

    const filterLabels = sourceData
      .filter(item => filterValue.indexOf(item.unitLayoutTypeId) >= 0)
      .map(item => ({
        label: item.name,
        value: item.unitLayoutTypeId
      }));

    return {
      title: `bedroom type${filterValue.length > 1 ? 's' : ''}:`,
      filterValue: filterLabels
    };
  }
}
