import {
  Component,
  OnInit,
  ChangeDetectorRef,
  OnDestroy,
  ViewChild,
  ElementRef,
  Output,
  EventEmitter,
  Input
} from '@angular/core';
import { FormControl } from '@angular/forms';
import 'rxjs/add/operator/debounceTime';
import { ViewLotteryResultMode } from '../../models/index.enum';
import { OrderFilterbyModel } from '../../models/order-filterby.model';

@Component({
  selector: 'app-lottery-result-header',
  templateUrl: './lottery-result-header.component.html',
  styleUrls: ['./lottery-result-header.component.scss']
})
export class LotteryResultHeaderComponent implements OnInit, OnDestroy {
  @Input() label: string;
  @Input() activeViewMode: ViewLotteryResultMode;
  @Input() isSearching:boolean;
  @Output() onChangeViewMode: EventEmitter<string> = new EventEmitter();
  @Output() onSearch: EventEmitter<string> = new EventEmitter();
  @Output() onFilter: EventEmitter<string> = new EventEmitter();
  @ViewChild('searchSection', { read: ElementRef }) searchSection: ElementRef;

  orderByOptions = [
      {
        label: 'Newest',
        value:{
        columnName: 'expiresInDays',
        ascending: true
      }},
      {
        label: 'Oldest',
        value:{
        columnName: 'expiresInDays',
        ascending: false
      }}
    ];


  // isSearchInputVisible = false;
  ViewMode = ViewLotteryResultMode;
  // searchInputControl: FormControl;
  filterSelectControl: FormControl;

  constructor(public cdRef: ChangeDetectorRef) {}

  ngOnInit() {
    // this.searchInputControl = new FormControl('');
    this.filterSelectControl = new FormControl(this.orderByOptions[0]);
    document.addEventListener('click', this.offClickHandler.bind(this));

    // this.searchInputControl.valueChanges
    //   .debounceTime(700)
    //   .subscribe(value => this.onSearch.emit(value));

    this.filterSelectControl.valueChanges.subscribe(value => this.onFilter.emit(value.value));
  }

  ngOnDestroy() {
    document.removeEventListener('click', this.offClickHandler.bind(this));
  }

  offClickHandler(event: any) {
    // if (!this.searchSection.nativeElement.contains(event.target)) {
    //   this.isSearchInputVisible = false; // need change to Observable maybe
    // }

    this.cdRef.markForCheck();
  }

  // onOpenSearchInput() {
  //   if (!this.isSearchInputVisible) {
  //     this.isSearchInputVisible = true;
  //   }
  // }

  onChangeViewModeFn(ViewModeValue: ViewLotteryResultMode) {
    this.activeViewMode = ViewModeValue;
    this.onChangeViewMode.emit(ViewModeValue);
  }
}
