import {Component, Inject, ElementRef, OnInit} from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {FormBuilder, FormGroup} from '@angular/forms';

import {FilterSelectorModal} from '../filter-selector-modal/filter-selector-modal.component';
import {tabsAnimations} from './neighborhoods-modal-animations';
import * as _ from 'lodash';

@Component({
  selector: 'neighborhoods-modal',
  templateUrl: './neighborhoods-modal.component.html',
  styleUrls: ['./neighborhoods-modal.component.scss'],
  animations: [tabsAnimations.translateTab]
})
export class NeighborhoodsModalComponent implements OnInit {
  private triggerElementRef: ElementRef;
  sourceData: any;
  displayName: string;
  filteredList: any;
  activeSubListIndex: number | null = null;
  subList: any[] = [];
  selectedNeighborhoods: any[];
  isActiveMainList = true;
  isActiveTreeList = false;

  constructor(
    public filterRef: MatDialogRef<FilterSelectorModal>,
    @Inject(MAT_DIALOG_DATA)
    public data: {
      triggerRef: ElementRef;
      sourceData: any;
      displayName: string;
      selectedNeighborhoods: any[];
    },
    private fb: FormBuilder
  ) {
    this.triggerElementRef = data.triggerRef;
    this.sourceData = data.sourceData;
    this.filteredList = data.sourceData;
    this.displayName = data.displayName;
    this.selectedNeighborhoods = data.selectedNeighborhoods;
  }

  ngOnInit() {
    const rect = this.triggerElementRef.nativeElement.getBoundingClientRect();
    this.filterRef.updatePosition({
      left: `${rect.left - 50}px`,
      top: `${rect.bottom + 20}px`
    });
  }

  private onDoneClick(): void {
    this.filterRef.close({data: this.selectedNeighborhoods});
  }

  onDeleteFilterTag(item) {
    this.deleteSelectedNeighborhoodFromList(item);
    item.isSelected = false;
  }

  deleteSelectedNeighborhoodFromList(item) {
    const deletedItemId = this.selectedNeighborhoods.indexOf(item);
    return this.selectedNeighborhoods.splice(deletedItemId, 1)[0];
  }

  onChangeFilterTagInput(value) {
    const inputValue = value.toLowerCase();

    if (inputValue) {
      this.isActiveTreeList = true;
      this.switchToMainList();

      this.filteredList = this.sourceData
        .map(item => {
          const _item = {...item};
          const filteredSubList = _item.subList.filter(subItem =>
            this.applyFilerToProps(subItem, inputValue)
          );

          _item.subList = [...filteredSubList];
          return _item;
        })
        .filter(item => item.subList.length || this.applyFilerToProps(item, inputValue));
    } else {
      this.isActiveTreeList = false;
      this.filteredList = this.sourceData;
    }
  }

  applyFilerToProps(lotteryObj, inputValue) {
    return (
      lotteryObj.title.toLowerCase().indexOf(inputValue) >= 0 ||
      (lotteryObj.id + '').indexOf(inputValue) >= 0
    );
  }

  onChangeNeighborhoodsItem(subItem) {
    if (!subItem.isSelected) {
      this.deleteSelectedNeighborhoodFromList(subItem);
      return;
    }

    // add sub item
    this.selectedNeighborhoods.push(subItem);
    const parentId = subItem.parentId;

    // remove parent selected item
    this.selectedNeighborhoods
      .filter(item => item.isSelected && item.id == parentId)
      .forEach(item => {
        this.deleteSelectedNeighborhoodFromList(item);
        item.isSelected = false;
      });
  }

  onChangeNeighborhoodsGroup(item) {
    if (!item.isSelected) {
      this.deleteSelectedNeighborhoodFromList(item);
      return;
    }

    this.selectedNeighborhoods.push(item);
    item.subList
      .filter(subItem => subItem.isSelected)
      .forEach(subItem => {
        this.deleteSelectedNeighborhoodFromList(subItem);
        subItem.isSelected = false;
      });
  }

  switchToSubList(listId) {
    this.isActiveMainList = false;
    this.isActiveTreeList = false;
    this.activeSubListIndex = listId;
    this.remapSubList();
  }

  remapSubList() {
    if (!this.filteredList.length) {
      this.subList = [];
      return;
    }

    this.subList =
      _.chain(this.filteredList)
        .filter(i => i.id === this.activeSubListIndex)
        .first()
        .get('subList')
        .value() || [];
  }

  switchToMainList() {
    this.isActiveMainList = true;
    this.activeSubListIndex = null;
  }

  getData() {
    return this.selectedNeighborhoods;
  }
}
