import { FilterEntityClass } from './filter.class';
import * as _ from 'lodash';

export class SubwayListFilterClass extends FilterEntityClass {
  selectedFields = [];

  constructor(data) {
    super(data);
    this.displayName = data.displayName;
    this.uiLabel = this.updateLabelSelected(this.selectedFields.length);
  }

  resetFilters() {
    this.selectedFields = [];
    this.sourceData.forEach(item => item.isSelected = false);
    this.uiLabel = this.updateLabel();
  }

  setModalResult(result) {
    this.selectedFields = result;
    this.uiLabel = this.updateLabel();
  }

  updateLabel() {
    const selectedCount = _.filter(this.selectedFields, 'isSelected').length;

    this.isSelected = selectedCount > 0;
    return this.updateLabelSelected(selectedCount);

    // return selectedCount ? `${selectedCount} Selected` : 'Any';
  }

  getClosedPopupData(modalRef) {
    return modalRef.subwayData;
  }

  getData() {
    return _.filter(this.selectedFields, 'isSelected');
  }

  populateData(data) {
    const fieldData = data.split(',');
    this.sourceData.forEach((item, idx) => {
      if (fieldData.includes(item.id)) {
        item.isSelected = true;
        this.selectedFields.push(item);
      }
    });
    this.uiLabel = this.updateLabel();
  }
}
