import { Component, ElementRef, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

@Component({
  selector: 'filter-selector-modal',
  templateUrl: './filter-selector-modal.component.html',
  styleUrls: ['./filter-selector-modal.component.scss']
})
export class FilterSelectorModal implements OnInit {
  private triggerElementRef: ElementRef;
  public filterForm: FormGroup;
  filterValues: any;
  displayName: string;
  selectedFields: any;

  constructor(
    public filterRef: MatDialogRef<FilterSelectorModal>,
    @Inject(MAT_DIALOG_DATA)
    public data: {
      triggerRef: ElementRef;
      displayName: string;
      sourceData: any;
      selectedFields: any;
    },
    private fb: FormBuilder
  ) {
    this.triggerElementRef = data.triggerRef;
    this.displayName = data.displayName;
    this.filterValues = data.sourceData;
    this.selectedFields = data.selectedFields;
    this.filterForm = fb.group({});
    this.buildForm();
  }

  public ngOnInit() {
    const rect = this.triggerElementRef.nativeElement.getBoundingClientRect();

    console.log(rect, rect.bottom);
    this.filterRef.updatePosition({
      left: `${rect.left - 50}px`,
      top: `${rect.bottom + 20}px`
    });
  }

  private onDoneClick(): void {
    this.filterRef.close({ data: this.filterForm.value });
  }

  private buildForm(): void {
     this.filterValues.forEach(field => {
        this.filterForm.addControl(
          field.fieldName,
          new FormControl(this.selectedFields[field.fieldName])
        );
      });
  }

  private selectAll(): void {
    Object.keys(this.filterForm.controls).forEach(key => this.filterForm.get(key).setValue(true));
  }

  private clearAll(): void {
    this.filterForm.reset();
  }

  isSelectAllActive() {
    return {
      active: Object.keys(this.filterForm.controls).some(key => {
        return this.filterForm.get(key).value === null;
      })
    };
  }

  isClearAllActive() {
    return {
      active: Object.keys(this.filterForm.controls).some(key => {
        return this.filterForm.get(key).value === true;
      })
    };
  }

  getFilterFormData() {
    return this.filterForm.value;
  }

  getData() {
      return this.filterForm.value;
  }
}
