@Component({
  selector: 'app-browse-lotteries-filters',
  templateUrl: './filters.component.html',
  styleUrls: ['./filters.component.scss']
})
export class BrowseLotteriesFiltersComponent implements OnInit, AfterViewInit, OnChanges {

  @Input() householdSize?: string;
  @Input() income?: string;
  @Input() householdType?: string;
  @Input() filterParams?: any;
  @Input() isRentalListing: boolean = true;
  @Input() isTransitioning:boolean;
  @Output() onChangeFiltersValues = new EventEmitter<any>();

  @ViewChild('householdIncomeButton') householdIncomeButtonRef: ElementRef;
  @ViewChild('maxPaymentButton') maxPaymentButtonRef: ElementRef;
  @ViewChild('neighborhoodsButton') neighborhoodsButtonRef: ElementRef;
  @ViewChild('bedroomsButton') bedroomsButtonRef: ElementRef;
  @ViewChild('buildingAmenitiesButton') buildingAmenitiesButtonRef: ElementRef;
  @ViewChild('nearbySubwaysButton') nearbySubwaysButtonRef: ElementRef;
  @ViewChild('nearbyPlacesButton') nearbyPlacesButtonRef: ElementRef;
  @ViewChild('householdSizeButton') householdSizeButtonRef: ElementRef;
  @ViewChild('propertyTypeButton') propertyTypeButtonRef: ElementRef;

  neighborhoodsList: any[] = [
    {title: 'Manhattan'},
    {title: 'Bronx'},
    {title: 'Brooklyn'},
    {title: 'Queens'},
    {title: 'Staten Island'},
  ];

  isFilterExpanded: boolean = true;
  activeModalRef: MatDialogRef<any>;
  usedFiltersCount = 0;
  dollarAmount = dollarAmount;
  filterMaps: any = {
    neighborhoods: new NeighborhoodsFilterClass({
      displayName: 'Neighborhoods',
      modalRef: NeighborhoodsModalComponent,
      sourceData: this.neighborhoodsList
    }),
    householdSize: new SingleFilterClass({
      displayName: 'Household Size',
      modalRef: SingleSelectModalComponent,
      formType: 'radio'
    }),
    bedrooms: new ListFilterClass({
      displayName: 'Number of Bedrooms',
      modalRef: FilterSelectorModal
    }),
    nearbySubways: new SubwayListFilterClass({
      displayName: 'Nearby Subways',
      modalRef: NearbySubwayModalComponent,
      sourceData: [
        {id: '1', isSelected: false, color: 'red'},
        {id: '2', isSelected: false, color: 'red'},
        {id: '3', isSelected: false, color: 'red'},
        {id: '4', isSelected: false, color: 'green'},
        {id: '5', isSelected: false, color: 'green'},
        {id: '6', isSelected: false, color: 'green'},
        {id: '7', isSelected: false, color: 'purple'},
        {id: 'A', isSelected: false, color: 'blue'},
        {id: 'C', isSelected: false, color: 'blue'},
        {id: 'E', isSelected: false, color: 'blue'},
        {id: 'B', isSelected: false, color: 'orange'},
        {id: 'D', isSelected: false, color: 'orange'},
        {id: 'F', isSelected: false, color: 'orange'},
        {id: 'M', isSelected: false, color: 'orange'},
        {id: 'G', isSelected: false, color: 'limeGreen'},
        {id: 'J', isSelected: false, color: 'brown'},
        {id: 'Z', isSelected: false, color: 'brown'},
        {id: 'L', isSelected: false, color: 'lightGray'},
        {id: 'S', isSelected: false, color: 'darkGray'},
        {id: 'N', isSelected: false, color: 'yellow'},
        {id: 'Q', isSelected: false, color: 'yellow'},
        {id: 'R', isSelected: false, color: 'yellow'},
        {id: 'W', isSelected: false, color: 'yellow'}
      ]
    }),
    propertyType: new ListFilterClass({
      displayName: 'Property Types',
      modalRef: FilterSelectorModal,
      sourceData: [
        {
          fieldName: '2',
          displayName: 'Condo/Coop'
        },
        {
          fieldName: '1',
          displayName: 'Small Home'
        }
      ]
    }),
    buildingAmenities: new ListFilterClass({
      displayName: 'Accommodations',
      modalRef: FilterSelectorModal
    }),
    nearbyPlaces: new ListFilterClass({
      displayName: 'Nearby Places',
      modalRef: FilterSelectorModal
    }),
    maxPayment: new MaxPaymentFilterClass({
      modalRef: MaxPaymentModalComponent
    }),
    householdIncome: new HouseholdIncomeFilterClass({
      modalRef: HouseholdIncomeModalComponent
    })
  };

  filterValues: any = {
    householdSize: '',
    householdIncome: '',
    householdType: '',
  };

  householdSizes: any[];

  householdTypes: any[] = [
    {name: "Sales", value: 1},
    {name: "Rentals", value: 2}
  ];

  constructor(
    public filter: MatDialog,
    public cdRef: ChangeDetectorRef,
    public overlay: Overlay,
    private lotteryApiService: LotteryApiService,
    private router: Router
  ) {
    //If navigation begins close active modal
    this.router.events.filter(event => event instanceof NavigationStart).subscribe(event => {
      this.closeActiveModal();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    //If switching between rentals/sales tab close modal
    if(changes.isTransitioning.currentValue){
      this.closeActiveModal();
    }
  }


  ngOnInit() {


    this.populateFields();
    forkJoin(
      this.lotteryApiService.getUnitLayoutTypes(),
      this.lotteryApiService.getHouseholdSizeOptions(),
      this.lotteryApiService.getNearbyPlaceTypes(),
      this.lotteryApiService.getActiveAmenities(),
      this.lotteryApiService.getNeighborhoods()
      //this.lotteryApiService.getHouseholdSizeOptions() //This is redundant
    ).subscribe(([
                   unitLayoutTypesResult, householdSizeOptionsResult, nearbyPlaceTypeResult, activeAmenitiesResult,
      neighborhoodResult
      //, householdSizOptionsResult  //This is redundant
                 ]) => {
      //const householdIncomeData = this.income;

      this.filterMaps.householdIncome.setData({income: this.income});

      const unitLayoutData = unitLayoutTypesResult.map(item => ({
        fieldName: item.name.replace(/\W/gi, '_'),
        displayName: item.name
      }));
      this.filterMaps.bedrooms.setData({sourceData: unitLayoutData});

      // HOUSEHOLD SIZE
      this.householdSizes = householdSizeOptionsResult;
      const householdSizeData = householdSizeOptionsResult.map(item => ({
        fieldName: item.value,
        displayName: item.name
      }));
      this.filterMaps.householdSize.setData({sourceData: householdSizeData});
      this.filterMaps.householdSize.updateLabel();

      const nearbyPlaceData = nearbyPlaceTypeResult.map(item => ({
        fieldName: item.placeType,
        displayName: item.placeType
      }));
      this.filterMaps.nearbyPlaces.setData({sourceData: nearbyPlaceData});


      const activeAmenitiesData = activeAmenitiesResult.map(item => ({
        fieldName: item.amenityTypeId,
        displayName: item.amenityName,
        amenityTypeId: item.amenityTypeId,
        amenityCategoryId: item.amenityCategoryId
      }));
      this.filterMaps.buildingAmenities.setData({sourceData: activeAmenitiesData});

      this.filterMaps.neighborhoods.sourceData.forEach((borough, index) => {
        borough.id = index + 1
        borough.subList = neighborhoodResult.filter(n => n.boroughId === borough.id).map(n => {
          n.parentId = borough.id;
          n.isSelected = false;
          //n.id = n.neighborhoodId;
          n.title = n.name;
          return n;
        });
      });

      // MAX PAYMENT/PRICE RANGE
      this.filterMaps.maxPayment.displayName = this.isRentalListing ? 'Max Monthly Rent' : 'Price Range';
      if (!this.isRentalListing) {
        this.filterMaps.maxPayment.min = 0;
        this.filterMaps.maxPayment.max = 1000000;
      }


      if (!this.cdRef["destroyed"]) this.cdRef.detectChanges();
    });
  }

  ngAfterViewInit() {
    _.keys(this.filterMaps).forEach(type => {
      this.filterMaps[type].triggerRef = this[`${type}ButtonRef`];
    });
    this.searchLotteries();
  }

  populateFields() {
    this.filterValues.householdSize = this.householdSize || null;
    this.filterValues.householdIncome = this.income || null;
    this.filterValues.householdType = (this.householdType) ? this.householdType : 1;
    console.log("Filter Params:", this.filterParams);
    this.setFilterValues(this.filterParams);
    this.cdRef.detectChanges();
  };

  setFilterValues(valuesMap) {
    if (valuesMap) {
      Object.keys(valuesMap).forEach(filterName => {
        this.filterMaps[filterName].populateData(valuesMap[filterName]);
      });
    }
  }

  clearFilterValues(){
    Object.keys(this.filterMaps).forEach(filterName => {
       this.filterMaps[filterName].resetFilters();
    });

  }

  openFilter(type): void {
    if (this.activeModalRef) {
      this.activeModalRef.close({
        data: this.activeModalRef.componentInstance.data.getClosedPopupData(
          this.activeModalRef.componentInstance,
        ),
        isAnotherModalOpen: true,
        modalType: type
      });

      return;
    }

    this.activeModalRef = this.filter.open(this.filterMaps[type].modalRef, {
      data: this.filterMaps[type],
      hasBackdrop: false,
      panelClass: 'filter-modal',
      scrollStrategy: this.overlay.scrollStrategies.reposition(),
      position: {'top': '0', 'left': '0'},
    });

    this.activeModalRef.beforeClose().subscribe(({ data, isAnotherModalOpen, modalType, activateSearch }) => {
      console.log("Model return:", data, isAnotherModalOpen, modalType, activateSearch);
      this.filterMaps[type].setModalResult(data);
      // this.updateUsedFiltersCount();
      this.activeModalRef = null;
      if (isAnotherModalOpen) {
        this.openFilter(modalType);
      }
      if (activateSearch) {
        this.emitFilterChanges();
      }
    });
  }

  //toggleFilterRow() {
  //  this.isFilterExpanded = !this.isFilterExpanded;
  //}

  searchLotteries() {
    if (this.activeModalRef) {
      this.closeActiveModal();
    } else {
      this.emitFilterChanges();
    }
    //this.toggleFilterRow();
  }

  getFiltersValues() {
    console.log('GETTING', this.filterMaps);
    return _.keys(this.filterMaps).reduce((obj, filterName) => {
      const filterData = this.filterMaps[filterName].getData();
      if (!_.isEmpty(filterData)) {
        obj[filterName] = filterData;
      }
      return obj;
    }, {});
  }

  updateUsedFiltersCount() {
    const allFilterData = this.getFiltersValues();
    this.usedFiltersCount = _.isEmpty(allFilterData) ? 0 : _.size(allFilterData);
  }

  updateQueryParams() {
    // const allFilterData = this.getFiltersValues();

    // const params = Object.keys(allFilterData).reduce((paramsObj, filterName) => {
    //   const formatingData = this.formattingToQueryParams(filterName, allFilterData);
    //   paramsObj[filterName] = formatingData;
    //   return paramsObj;
    // }, {});

    // console.log('PARAMS', params)
    // this.router.navigate(
    //   ['/search-businesses'],
    //   {
    //     queryParams: {
    //       ...this.filterValues,
    //       ...params
    //     }
    //   }
    // );
  }

  formattingToQueryParams(filterName: string, filterData) {
    let mappingData;
    if (filterName === "nearbySubways") {
      mappingData = filterData[filterName].map(subway => subway.id).join(',');
    } else if (filterName === "neighborhoods") {
      mappingData = filterData[filterName].map(item => item.title).join(',');
    } else if (filterName === "maxPayment") {
      mappingData = filterData[filterName].join(',');
    } else if (filterName === "householdIncome") {
      mappingData = filterData[filterName];
    } else if (filterName === "householdSize") {
      mappingData = filterData[filterName];
    } else {
      mappingData = Object.keys(filterData[filterName]).join(',');
    }
    console.log('filtering', filterName, filterData)

    return mappingData;
  }

  closeActiveModal() {
    if (this.activeModalRef) {
      this.activeModalRef.close({
        data: this.activeModalRef.componentInstance.getData(),
        activateSearch: true,
      });
    }
  }

  emitFilterChanges() {
    //For now we only want to emit event for rental lotter searches
    //TODO: Update this component properly for rental and sales type lottery listings
    //if (this.isRentalListing) {
      this.onChangeFiltersValues.emit({
        ...this.filterValues,
        ...this.getFiltersValues()
      });
      this.updateQueryParams();
      this.updateUsedFiltersCount();
    //}
  }
}
