import {Component, OnInit, Input, ChangeDetectorRef} from '@angular/core';
import {ViewLotteryResultMode} from '../../models/index.enum';
import {OrderFilterbyModel} from '../../models/order-filterby.model';
import {Router} from '@angular/router';

@Component({
  selector: 'app-lottery-result-list',
  templateUrl: './lottery-result-list.component.html',
  styleUrls: ['./lottery-result-list.component.scss']
})
export class LotteryResultListComponent implements OnInit {
  @Input() lotteryResultData: any;
  @Input() viewModeActive: any;
  @Input() orderBy: OrderFilterbyModel;
  @Input() paginatorId: string;
  @Input() lotteriesSortOrder: string;
  ViewMode = ViewLotteryResultMode;
  cardViewType: string = 'summary';

  enteredLotteryId: string;
  p: number = 1; //page number
  pageSize: number = 12;

  constructor(
    private cd: ChangeDetectorRef,
    private router: Router
  ) {
  }

  ngOnInit() {
  }

  onLotteryClickEvent(lottery) {
    let id = lottery.details.lotteryId;
    this.enteredLotteryId = id;
    this.router.navigate(['/details/' + id]);
  }

  pageChanged($event) {
    this.p = $event;
  }
}
