import {MatDialogRef} from "@angular/material";
import {Component, ElementRef} from '@angular/core';

export interface FilterEntityData {
  displayName: string,
  modalRef?: MatDialogRef<Component>,
  sourceData?: any
}

export interface FilterEntityInterface {
  modalRef: MatDialogRef<Component>;
  sourceData?: any;
  displayName: string;
  triggerRef: ElementRef;
  isSelected: boolean;
  uiLabel: string;

  // constructor(data: FilterEntityData): void;
  setData(data: FilterEntityData);
  updateLabelSelected(value: any): string;
  updateLabel(): void;
  setModalResult(data: any): void;
  getData(): any;
}


export class FilterEntityClass implements FilterEntityInterface {
  modalRef = {} as MatDialogRef<Component>;
  sourceData = [];
  displayName = '';
  triggerRef = {} as ElementRef;
  isSelected = false;
  uiLabel = '';

  constructor(data: FilterEntityData) {
    this.setData(data);
  }

  setData(data: FilterEntityData) {
    if (!data) {
      return false;
    }

    Object.keys(data).forEach(name => {
      if (data[name] != null && typeof data[name] !== 'undefined' && this.hasOwnProperty(name)) {
        this[name] = data[name];
      }
    });
  }

  resetFilters() {
    console.log('not implemented');
  }

  updateLabelSelected(count) {
    return count ? count === this.sourceData.length ? 'All' : `${count} Selected` : 'Any';
  }

  updateLabel() {
  }

  setModalResult(data) {
  }

  getData(){}
}
