import { Component, OnInit, ElementRef, Inject } from '@angular/core';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'app-single-select-modal',
  templateUrl: './single-select-modal.component.html',
  styleUrls: ['./single-select-modal.component.scss']
})
export class SingleSelectModalComponent implements OnInit {

  private triggerElementRef: ElementRef;
  public filterForm: FormGroup;
  filterValues: any;
  displayName: string;
  selectedField: any;
  controlName:string;
  isRadioButtonGroup:boolean = true;

  constructor(
    public filterRef: MatDialogRef<SingleSelectModalComponent>,
    @Inject(MAT_DIALOG_DATA)
    public data: {
      triggerRef: ElementRef;
      displayName: string;
      sourceData: any;
      selectedField: any;
      isRadioGroup:boolean;
    },
    private fb: FormBuilder
  ) {
    this.triggerElementRef = data.triggerRef;
    this.displayName = data.displayName;
    this.filterValues = data.sourceData;
    this.selectedField = data.selectedField;
    this.isRadioButtonGroup = data.isRadioGroup;
    this.controlName = data.displayName.replace(/\s/g, ""); //remove any whitespace in string to set as formcontrolname
    this.filterForm = fb.group({});
    this.buildForm();
  }

  public ngOnInit() {
    const rect = this.triggerElementRef.nativeElement.getBoundingClientRect();

    console.log(rect, rect.bottom);
    this.filterRef.updatePosition({
      left: `${rect.left - 50}px`,
      top: `${rect.bottom + 20}px`
    });
  }

  private onDoneClick(): void {
    this.filterRef.close({ data: this.filterForm.value });
  }

  private buildForm(): void {
    this.filterForm.addControl(this.controlName, new FormControl(this.selectedField));
  }


  getFilterFormData() {
    return this.filterForm.value;
  }

  getData() {
    return this.filterForm.get(this.controlName).value;
  }

}
