  updateLotteryResultData(lotteries) {
    return lotteries.map((item, index) => {
      const subwayArray = item.trains
        ? _.uniq(item.trains.split(',')).sort()
        : [];
      const markers = [];
      for (let i = 0; i < Math.ceil(Math.random() * 3); i++) {
        const randomNumber: any = (Math.random() * (0.001 - 0.006) + 0.006).toFixed(3);
        const randomNumber2: any = (Math.random() * (0.001 - 0.006) + 0.006).toFixed(3);
        const lang = parseFloat((40.728225 + index * randomNumber).toFixed(6));
        const long = parseFloat((-73.799986 + index * randomNumber2).toFixed(6));
        const latlng = new google.maps.LatLng(lang, long)
        markers.push({latlng});
      }

      const entity = {
        mainImage: (item.defaultPhotoStream)
          ? (item.defaultPhotoStream)
          : '/assets/images/demo-lottery-house.png',
        markers,
        details: {
          lotteryId: item.lotteryId,
          street: item.lotteryName,
          area: (
            (item.neighborhood)
              ? item.neighborhood
              : 'Neighborhood'
          ) + ' | ' + ((item.borough) ? item.borough : 'Borough'),
          subwayServices: subwayArray,
          images: [
            (item.defaultPhotoStream) ? (item.defaultPhotoStream) : '/assets/images/demo-lottery-house.png'
          ],
          lotteryName: item.lotteryName.trim(),
          isAddedToFavorite: false,
          expirationDate: new Date(),
          expirationDateText: (item.endIn > 0) ? ('Lottery closing in ' + item.endIn + ' days') : ((item.endIn === 0) ? ('Lottery ends today') : ('')),
          expiresInDays: item.endId,
          minIncome: item.minIncome,
          maxIncome: item.maxIncome,
          minHousehold: item.minHouseholdSize,
          maxHousehold: item.maxHouseholdSize,
          mobilityPref: item.mobility,
          units: item.units,
          studios: item.studios,
          oneBR: item.oneBR,
          twoBR: item.twoBR,
          threeBR: item.threeBR,
          visionHearingPref: item.visionHearing
        }
      };
      return entity;
    });
  }

