  getLotteriesForSalesAndRental() {
    const lotterySearchModel = {
      UnitTypes: [],
      NearbyPlaces: [],
      NearbySubways: [],
      Amenities: [],
      Boroughs: [],
      HouseholdSize: this.householdSize,
      Income: this.income.replace(/,/g, ''),
      HouseholdType: this.householdType
    };

    // Rental Search: Here for now until refactor
    this.lotteryApiService.searchLotteries(lotterySearchModel)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(lotteries => {
        this.lotteryResultData = this.updateLotteryResultData(lotteries); //Rental Lottery Result Data
        this.isSearching = false;
        console.log(this.lotteryResultData);
        // this.cdRef.detectChanges();
      });

    // Sales Search: Here for now until refactor
    let tempSearchModel = {...lotterySearchModel} as any;
    tempSearchModel.OwnerTypes = [];
    tempSearchModel.HouseholdType = 1;
    this.lotteryApiService.searchLotteries(tempSearchModel)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(lotteries => {
        this.salesResultData = this.updateLotteryResultData(lotteries); //Sales Lottery Result Data
        this.isSearchingSales = false;
      });
  }

  createNeighborhoodsDataModel(neighborhoodApiData) {
    return [
      {title: 'Manhattan'},
      {title: 'Bronx'},
      {title: 'Brooklyn'},
      {title: 'Queens'},
      {title: 'Staten Island'},
    ].map((borough: any, index) => {
      borough.id = index + 1;
      borough.subList = neighborhoodApiData
        .filter(n => n.boroughId === borough.id)
        .map(n => {
          n.parentId = borough.id;
          n.title = n.name;
          return n;
        });
      return borough;
    });
  }

