import { FilterEntityClass } from './filter.class';

export class NeighborhoodsFilterClass extends FilterEntityClass {
  inputValue = '';
  selectedNeighborhoods = [];

  constructor(value) {
    super(value);
    this.uiLabel = this.updateLabelSelected(this.selectedNeighborhoods.length);
  }

  resetFilters() {
    this.inputValue = '';
    this.selectedNeighborhoods = [];
    this.sourceData.forEach(item => item.isSelected = false);
    this.isSelected = false;
    this.uiLabel = this.updateLabel();
  }

  setModalResult() {
    this.isSelected = this.selectedNeighborhoods.length > 0;
    this.uiLabel = this.updateLabel();
  }

  getClosedPopupData() {
    return this.selectedNeighborhoods;
  }

  getData() {
    return this.selectedNeighborhoods;
  }

  updateLabel() {
    return this.selectedNeighborhoods.length ? `${this.selectedNeighborhoods.length} Selected` : 'Any';
  }

  populateData(data) {

    const fieldData = data.split(',');
    this.sourceData.forEach(borough => {
      if (fieldData.includes(borough.title)) {
        this.selectedNeighborhoods.push(borough);
        borough.isSelected = true;
      }
      borough.subList.forEach(subBorough => {
        if (fieldData.includes(subBorough.name)) {
          this.selectedNeighborhoods.push(subBorough);
          subBorough.isSelected = true;
        }
      });
    });
    this.setModalResult();
  }
}
