import {FilterEntityClass} from './filter.class';
import {CurrencyPipe} from '@angular/common';

export class HouseholdIncomeFilterClass extends FilterEntityClass {
  householdIncome: number = null;
  currencyPipe: CurrencyPipe = new CurrencyPipe('en-US');

  constructor(value) {
    super(value);
    this.uiLabel = this.updateLabel();
  }

  resetFilters() {
    this.householdIncome = null;
    this.isSelected = false;
    this.uiLabel = this.updateLabel();
  }

  setModalResult(income) {
    this.householdIncome = income;

    this.isSelected = this.householdIncome != null;
    this.uiLabel = this.updateLabel();
  }

  updateLabel() {
    return this.householdIncome ? this.currencyPipe.transform(this.householdIncome, '', true, '0.0') : 'Any';
  }

  getClosedPopupData(modalRef) {
    return modalRef.householdIncome;
  }

  getData() {
    return this.householdIncome;
  }

  populateData(data) {
    this.householdIncome = data;
    this.isSelected = this.householdIncome != null;
    this.uiLabel = this.updateLabel();
  }
}
