import { FilterEntityClass } from './filter.class';

export class MaxPaymentFilterClass extends FilterEntityClass {
  min = 0;
  max = 10000;
  value = [];

  constructor(value) {
    super(value);
    this.uiLabel = this.updateLabel();
  }

  resetFilters() {
    this.value = [];
    this.isSelected = false;
    this.uiLabel = this.updateLabel();
  }

  setModalResult(data) {
    this.value = data;

    this.isSelected = this.value.length > 0;
    this.uiLabel = this.updateLabel();
  }

  updateLabel() {
    return this.value.length ? `$ ${this.value[0]} - ${this.value[1]}` : 'Any';
  }

  getClosedPopupData(modalRef) {
    return modalRef.sliderRange;
  }

  getData() {
    return this.value;
  }

  populateData(data) {
    const sortedValues = data.split(',').filter(val => !isNaN(val)).map(val => Number.parseInt(val)).sort().reverse();
    this.max = (sortedValues[0] && sortedValues[0] <= 10000) ? sortedValues[0] : 10000;
    this.min = (sortedValues[1] && 0 < sortedValues[1] && sortedValues[1] < 10000) ? sortedValues[1] : 0;
    this.value = [this.min, this.max];
    this.isSelected = this.value.length > 0;
    this.uiLabel = this.updateLabel();
  }
}
